@tailwind components;
@tailwind utilities;

* {
  background-color: #1e1e1e;
  font-family: "Manrope", sans-serif;
  font-weight: 500;
  color: white;
}

h1 {
  font-family: "Manrope", sans-serif;
  @apply text-3xl;
}

h2 {
  @apply text-2xl;
}

h3 {
  @apply text-xl;
}

h4 {
  @apply text-lg;
}
